<template>
  <div class="home">
    <HelloWorld :settings="settingObj"/>
    <div class="sidebarUnhide">
      >>
      <div class="sidebar">
        <div v-for="(item, name) in settingObj" :key="name">
          <label>{{name}}</label>:<input type="text" v-model="settingObj.item"> {{item.value}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
// import imageTrace from '@/components/imageTrace.vue'

export default {
  name: 'Home',
  components: {
    HelloWorld
    // imageTrace
  },
  data:function(){
    return{
      settingObj:{
        circleNumbers:800 ,            //how many circles
        maxRadius:50,                   //max radius before burst
        defaultRadius:4,                //default circle size
        detectRadius:50,                //current area of mouse point
        defaultDetectRadius:50,         //defaule area of mouse point
        maxDetectRadius:200,            //max area of mouse point
        burstRadius:120,                //how large when burst
        circleRespawnCountdown:300,     //respawn time in frames
        powRespawnCountdown:0,
        burstSpeed:8,                   //how quick circles burst
        powerUpCounts:1,
      }
    }
  },
}
</script>
<style scoped>
.home{
  margin:0;
}

.sidebarUnhide{
  position: fixed;
  top:10px;
  width:10px;
  transition: all 0.15s linear;
}

.sidebar{
  display: none;
  min-height: 200px;
  padding:10px;
}

.sidebarUnhide:hover{
  transition: all 0.15s linear;
  width:200px;
}

.sidebarUnhide:hover .sidebar{
  background-color: #FFFFFFAA;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}

#logo{
  animation: fadein 2s linear;
}

@keyframes fadein{
  0% {
    opacity: 0;
    /* transform: scale(.8); */
  }
  50%{
    opacity: 1;
  }
  100% {
    opacity: .5;
    /* transform: scale(1); */
  }
}


</style>